/* eslint-disable no-unused-vars */
import React, { useState, useEffect } from 'react';
import { Route, Routes } from 'react-router-dom';
import {
	Container, Grow, Typography, AppBar, Grid,
} from '@mui/material';

import Form from './components/Form/Form.jsx';
import Posts from './components/Posts/Posts.jsx';

import memories from './images/memories.png';
import { MenuBar, Heading, Image } from './styles.js';

import './styles.css';

const App = () => (
	<Container maxWidth="lg">
		<AppBar className="menu-bar" position="static" color="inherit">
			<Typography color="rgba(0,183,255, 1)" variant="h2" align="center">Memories</Typography>
			<Image src={memories} alt="memories" height={30} />
		</AppBar>
		<Grow in>
			<Container>
				<Grid container justifyContent="space-between" alignItems="stretch" spacing={3} >
					<Grid item xs={12} sm={7}>
						<Posts />
					</Grid>
					<Grid item xs={12} sm={4}>
						<Form />
					</Grid>
				</Grid>
			</Container>
		</Grow>
	</Container>
);

export default App;
